    --tw-space-y-reverse: 0;
    margin-top: calc(0px * calc(1 - var(--tw-space-y-reverse)));
    margin-bottom: calc(0px * var(--tw-space-y-reverse));
  }

  #eddesk-app .lg\:pb-32 {
    padding-bottom: 8rem;
  }

  #eddesk-app .lg\:pt-48 {
    padding-top: 12rem;
  }

  #eddesk-app .lg\:text-left {
    text-align: left;
  }

  #eddesk-app .lg\:text-9xl {
    font-size: 8rem;
    line-height: 1;
  }
}

@media (min-width: 1280px) {

  #eddesk-app .xl\:block {
    display: block;
  }
}
